  content: "";
  height: 10px;
  position: absolute;
  right: -5px;
  top: 7px;
  width: 3px;
}

@-webkit-keyframes charge {
  0% {
    box-shadow: inset 0px 0px 0px #2E2E2E;
  }
  100% {
    box-shadow: inset 60px 0px 0px #2E2E2E;
  }
}

@-moz-keyframes charge {
  0% {
    box-shadow: inset 0px 0px 0px #2E2E2E;
  }
  100% {
    box-shadow: inset 60px 0px 0px #2E2E2E;
  }
}

@keyframes charge {
