	For Small Desktop
==============================================================*/
@media (min-width: 980px) and (max-width: 1150px) {
  /*about*/
  #about .welcome-block img {
    margin-bottom: 30px;
  }
  /* portfolio */
  figcaption.mask {
    bottom: -151px;
  }
  .testimonial-item > div:after {
    bottom: -40px;
  }
}

/*============================================================
	Tablet (Portrait) Design for a width of 768px
==============================================================*/
@media (min-width: 768px) and (max-width: 979px) {
  /* home slider  */
  .caption-content h2 {
    font-size: 40px;
  }
  .caption-content p {
    font-size: 25px;
  }
  .caption-content strong {
    font-size: 45px;
  }
  /* about */
  .recent-works {
    margin-bottom: 50px;
  }
  .service-item {
    margin-bottom: 50px;
  }
  /* portfolio */
  .portfolio-item {
    width: 48%;
  }
  /* testimonial */
  .testimonial-item {
    width: 100%;
  }
  .testimonial-item {
    width: 80%;
  }
  .testimonial-item > div:after {
    bottom: -35px;
  }
  /* price */
  .price-table {
    margin-bottom: 50px;
  }
  /* contact form */
  .contact-form {
    margin-bottom: 50px;
  }
}

/*============================================================
	Mobile (Portrait) Design for a width of 320px
==============================================================*/
@media only screen and (max-width: 767px) {
  body {
    font-size: 14px;
  }
  .parallax {
    background-position: centet tip !important;
  }
  .sec-title h2 {
    font-size: 25px;
  }
  .sec-title h2:after {
    left: 30%;
  }
  /*navigation*/
  .navbar-inverse .navbar-toggle {
    border-color: #fff;
  }
  .navbar-inverse .navbar-toggle:hover,
  .navbar-inverse .navbar-toggle:focus {
    background-color: transparent;
  }
  /* slider */
  .caption-content h2 {
    font-size: 18px;
  }
  .caption-content > span {
    font-size: 16px;
    margin-bottom: 20px;
  }
  .caption-content p {
    font-size: 16px;
    margin-bottom: 30px;
  }
  .caption-content strong {
    font-size: 22px;
  }
  /* about */
  .recent-works {
    margin-bottom: 85px;
  }
  #about h3 {
    font-size: 18px;
    margin: 0 0 35px !important;
  }
  #about .owl-buttons {
    margin-top: 20px;
  }
  #about .message-body {
    margin-bottom: 45px;
  }
  #about .welcome-block img {
    margin: 0 25px 15px 0;
  }
  /* service */
  .service-item {
    width: 100%;
  }
  /*portfolio*/
  .portfolio-item {
    margin: 0 auto 10px;
    font-size: 14px;
    width: 280px;
  }
  figcaption.mask {
    bottom: -107px;
    color: #fff;
    padding: 15px;
  }
  /* lightbox */
  .fancybox-title h3 {
    font-size: 20px;
  }
  .fancybox-title p {
    font-size: 14px;
  }
  /* testimonial */
  .testimonial-item {
    width: 95%;
  }
  .testimonial-item > div:before,
  .testimonial-item > div:after {
    background-image: none;
  }
  .testimonial-item > div > span {
    margin: 30px 0 20px;
  }
  /* price */
  .price-table {
    margin-bottom: 50px;
  }
  /* follow us */
  .social-button li:nth-child(2) {
    margin: 0 25px;
  }
  .social-button li a {
    height: 65px;
    line-height: 71px;
    width: 65px;
  }
  /* contact form */
  .contact-form {
    margin-bottom: 50px;
  }
  /* footer */
  .footer-content {
    width: 100%;
  }
  .footer-social ul li {
    margin: 0 7px;
  }
}

/*============================================================
	Mobile (Landscape) Design for a width of 480px
==============================================================*/
@media only screen and (min-width: 480px) and (max-width: 767px) {
  .sec-title h2:after {
    left: 38%;
  }
  /* home slider */
  .caption-content h2 {
    font-size: 35px;
  }
  .caption-content p {
    font-size: 25px;
  }
  .caption-content strong {
    font-size: 35px;
  }
  /*about*/
  #about .welcome-block h3 {
    margin: 0 0 35px;
  }
  /* service */
  .service-item {
    margin: 0 auto 50px;
    width: 55%;
  }
  /* portfolio */
  .portfolio-item {
    width: 48%;
  }
  figcaption.mask {
    bottom: -132px;
  }
  /* testimonial */
  .testimonial-item {
    width: 80%;
  }
  .testimonial-item > div:before {
    bottom: 160px;
  }
  /* social */
  .social-button li:nth-child(2) {
    margin: 0 50px;
  }
  .social-button li a {
    height: 90px;
    line-height: 96px;
    width: 90px;
  }
  /* price */
  .price-table {
    margin-bottom: 50px;
  }
  /* contact form */
  .contact-form {
    margin-bottom: 50px;
  }
  /* footer */
  .footer-content {
    width: 380px;
  }
}
